import AppBody from './appBody/appBody';
import FrameFactory from './frameFactory/frameFactory';
import Canvas from './canvas/canvas';

export default class AnimationPlayer {
  constructor() {
    this.frameCount = 0;
    this.canvas = null;
    this.framesList = null;
    this.addFrameButton = null;
    this.currentFrame = null;
  }

  init() {
    AppBody.draw();
    FrameFactory.draw();
    this.indexing();
    this.frameCount = 1;
    this.currentFrame = document.getElementsByClassName('app-frames__item')[this.frameCount - 1];
    this.currentFrame.getElementsByClassName('delete-frame')[0].style.display = 'none';
    this.canvas = new Canvas();
    this.canvas.init();
    this.addFrameButton = document.getElementById('add-frame');
    this.addFrameButton.addEventListener('click', () => {
      this.addFrame();
      this.indexing();
      this.canvas.setCurrentFrame(this.currentFrame);
      this.canvas.clear();
    });
    this.framesList = document.getElementById('app-frames');
    this.framesList.addEventListener('click', (e) => {
      if (e.target.classList.contains('canvas-background')) {
        this.setFrame(e);
      } else if (e.target.classList.contains('delete-frame')) {
        this.deleteFrame(e);
      } else if (e.target.classList.contains('duplicate-frame')) {
        this.duplicateFrame(e);
      }
    });
    this.rangeFps = document.getElementById('fps');
    this.rangeFps.addEventListener('change', (e) => {
      const fps = e.target.value;
      document.getElementsByClassName('fps-label')[0].innerHTML = fps;
      this.canvas.setFps(fps);
    });
  }

  addFrame() {
    this.currentFrame.classList.remove('selected');
    FrameFactory.draw();
    this.frameCount += 1;
    this.currentFrame = document.getElementsByClassName('app-frames__item')[this.frameCount - 1];
  }

  indexing() {
    this.frames = Array.from(document.getElementsByClassName('app-frames__item'));
    this.frames.forEach((frame, i) => {
      const canvas = frame.getElementsByClassName('canvas-background')[0];
      const index = frame.getElementsByClassName('frame-count')[0];
      canvas.setAttribute('data-index', i);
      index.innerHTML = i + 1;
    });
  }

  setFrame(e) {
    const frame = document.getElementsByClassName('app-frames__item')[e.target.dataset.index];
    if (frame !== this.currentFrame) {
      this.currentFrame.classList.remove('selected');
      this.currentFrame = frame;
      this.currentFrame.classList.add('selected');
      this.canvas.setCurrentFrame(this.currentFrame);
      this.canvas.loadData();
    }
  }

  deleteFrame(e) {
    const frame = e.target.parentElement;
    this.canvas.deleteData(frame);
    if (frame === this.currentFrame) {
      frame.remove();
      this.frameCount -= 1;
      this.currentFrame = document.getElementsByClassName('app-frames__item')[this.frameCount - 1];
    } else {
      frame.remove();
      this.frameCount -= 1;
    }
    this.currentFrame.classList.add('selected');
    this.indexing();
    this.canvas.setCurrentFrame(this.currentFrame);
    this.canvas.loadData();
  }

  duplicateFrame(e) {
    const frame = e.target.parentElement;
    this.canvas.duplicateData(frame);
    this.currentFrame.classList.remove('selected');
    FrameFactory.duplicate(frame);
    this.frameCount += 1;
    this.indexing();
    this.currentFrame = frame.nextSibling.nextElementSibling;
    this.currentFrame.classList.add('selected');
    this.canvas.setCurrentFrame(this.currentFrame);
    this.canvas.loadData();
    this.canvas.updateFrame();
  }
}
