export default class AppBody {
  constructor() {
    this.ctx = document.getElementById('canvas');
    this.canvas = this.ctx.getContext('2d');
    this.color = null;
    this.frame = null;
    this.currentFrame = 0;
    this.imageData = [];
    this.preview = document.getElementById('preview');
    this.fps = 1;
    this.status = false;
  }

  init() {
    [this.frame] = document.getElementsByClassName('frame-canvas');
    this.imageData[this.currentFrame] = this.canvas.getImageData(0, 0, 480, 480);
    const opts = {
      color: 'hsl(hue,100%,40%)',
      radius: 30,
    };
    let tick = 0;
    let currentHue = 0;

    let painting = false;

    this.ctx.onmousedown = () => {
      painting = true;
    };
    this.ctx.onmouseup = () => {
      painting = false;
      this.updateFrame();
      this.imageData[this.currentFrame] = this.canvas.getImageData(0, 0, 480, 480);
    };
    this.ctx.addEventListener('mousemove', (e) => {
      const posX = e.pageX - this.ctx.offsetLeft;
      const posY = e.pageY - this.ctx.offsetTop;

      if (painting) {
        tick += 1;
        if (!(tick % 1)) {
          if ((currentHue !== 356)) {
            currentHue += 1;
          } else {
            currentHue = 0;
          }
        }

        this.color = opts.color.replace('hue', currentHue);
        this.canvas.fillStyle = this.color;
        this.canvas.beginPath();
        this.canvas.arc(posX, posY, opts.radius, 0, Math.PI * 2);
        this.canvas.fill();
      }
    });
    this.animation();

    document.addEventListener('keypress', (e) => {
      if (e.keyCode === 13) {
        if (!document.fullscreenElement) {
          this.preview.requestFullscreen();
        } else if (document.exitFullscreen) {
          document.exitFullscreen();
        }
      }
    }, false);
  }

  updateFrame() {
    const frameCanvas = this.frame.getContext('2d');
    frameCanvas.drawImage(this.ctx, 0, 0, 96, 96);
  }

  setCurrentFrame(frame) {
    [this.frame] = frame.getElementsByClassName('frame-canvas');
    this.currentFrame = frame.getElementsByClassName('canvas-background')[0].dataset.index;
    if (!this.imageData[this.currentFrame]) {
      this.clear();
      this.imageData[this.currentFrame] = this.canvas.getImageData(0, 0, 480, 480);
    }
  }

  clear() {
    this.canvas.clearRect(0, 0, 480, 480);
  }

  loadData() {
    this.canvas.putImageData(this.imageData[this.currentFrame], 0, 0);
  }

  deleteData(frame) {
    const dataIndex = frame.getElementsByClassName('canvas-background')[0].dataset.index;
    this.imageData.splice(dataIndex, 1);
  }

  duplicateData(frame) {
    const dataIndex = frame.getElementsByClassName('canvas-background')[0].dataset.index;
    const data = this.imageData[dataIndex];
    this.imageData.splice(dataIndex, 0, data);
  }

  animation() {
    const canvas = document.createElement('canvas');
    canvas.width = 480;
    canvas.height = 480;
    const previewCanvas = this.preview.getContext('2d');
    let count = 0;
    const interval = setInterval(() => {
      canvas.getContext('2d').clearRect(0, 0, 480, 480);
      canvas.getContext('2d').putImageData(this.imageData[count], 0, 0);
      previewCanvas.clearRect(0, 0, 240, 240);
      previewCanvas.drawImage(canvas, 0, 0, 240, 240);
      if (this.status) {
        clearInterval(interval);
        this.status = false;
        this.animation();
      }
      if (count === this.imageData.length - 1) {
        count = 0;
      } else {
        count += 1;
      }
    }, 1000 / this.fps);
  }

  setFps(fps) {
    this.fps = fps;
    this.status = true;
  }
}
